import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.baseCargo}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新增货主', modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {shipperCode, shipperName}
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>货主编号：</label>
              <div className='box'>
                <Input value={shipperCode} onChange={(e) => {
                  this.onInputChange({shipperCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>货主名称：</label>
              <div className='box'>
                <Input value={shipperName} onChange={(e) => {
                  this.onInputChange({shipperName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={12}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" onClick={this.handleAdd}>新增</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



