import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPERS_PAGE, API_SHIPPERS_CREATE, API_SHIPPERS_UPDATE, API_SHIPPERS_DELETE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.SHIPPERS_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.SHIPPERS_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_SHIPPERS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.SHIPPERS_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.SHIPPERS_FILE
    })
  })
}

// 创建/编辑货主
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.SHIPPERS_UPDATE, data: {modalTuttonLoading: true}})
  let api = reqData.data && reqData.data.id ? API_SHIPPERS_UPDATE : API_SHIPPERS_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.SHIPPERS_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.SHIPPERS_FILE
    })
  })
}

// 删除货主
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_SHIPPERS_DELETE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.SHIPPERS_FILE
    })
  })
}


