import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button
} from 'antd'
import {popupFormItemLayout} from 'enum'
import {CsxFormSelect} from 'Form'
import {CsxFormCascader} from 'Form'
import {API_SHIPPERS_SHIPPERTYPES, API_DISTRICE_LIST} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.baseCargo}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.shipperType = {
        value: reqObj.shipperType && reqObj.shipperType.toString(),
        children: reqObj.shipperTypeDesc && reqObj.shipperTypeDesc.toString()
      }
      // 三级联动还原
      let value = []
      let children = []
      reqObj.provinceCode && (value.push({value: reqObj.provinceCode, label: reqObj.provinceName}), children.push(reqObj.provinceCode))
      reqObj.cityCode && (value.push({value: reqObj.cityCode, label: reqObj.cityName}), children.push(reqObj.cityCode))
      reqObj.regionCode && (value.push({value: reqObj.regionCode, label: reqObj.regionName}), children.push(reqObj.regionCode))
      reqObj.threeCode = {value, children}

      update({modalData: {...reqObj}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        // 特殊处理三级联动的值
        reqObj.threeCode[0] && (reqObj.provinceCode = reqObj.threeCode[0].value, reqObj.provinceName = reqObj.threeCode[0].label)
        reqObj.threeCode[1] && (reqObj.cityCode = reqObj.threeCode[1].value, reqObj.cityName = reqObj.threeCode[1].label)
        reqObj.threeCode[2] && (reqObj.regionCode = reqObj.threeCode[2].value, reqObj.regionName = reqObj.threeCode[2].label)
        delete reqObj.threeCode

        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <FormItem
            {...popupFormItemLayout}
            label="货主编号"
            hasFeedback
          >
            {getFieldDecorator('shipperCode', {
              initialValue: modalData.shipperCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <Input disabled={modalData.id ? true : false} />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="货主名称"
            hasFeedback
          >
            {getFieldDecorator('shipperName', {
              initialValue: modalData.shipperName,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <Input disabled={modalData.id ? true : false} />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="货主类型"
            hasFeedback
          >
            {getFieldDecorator('shipperType', {
              initialValue: modalData.shipperType,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSelect
                dateApi={API_SHIPPERS_SHIPPERTYPES}
                selectItem={{key: 'key', valueKey: 'value'}}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="行政区"
            hasFeedback
          >
            {getFieldDecorator('threeCode', {
              initialValue: modalData.threeCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormCascader
                dateApi={API_DISTRICE_LIST}
                searchParams={{type: 2}}
                size={5}
                selectItem={{key: 'code', valueKey: 'name'}}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="联系人"
            hasFeedback
          >
            {getFieldDecorator('contactPerson', {
              initialValue: modalData.contactPerson
            })(
              <Input />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="联系电话"
          >
            {getFieldDecorator('contactPhone', {
              initialValue: modalData.contactPhone
            })(
              <Input />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="地址"
            hasFeedback
          >
            {getFieldDecorator('shipperAddress', {
              initialValue: modalData.shipperAddress
            })(
              <Input />
            )}
          </FormItem>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



